@use 'base/variables' as v;
@use 'base/mixins' as m;

.producto {
    &__formulario {
        display: flex;
        flex-direction: column;
    }
    &__label {
        font-size: 2.2rem;
        margin-right: 1rem;
    }
    &__cantidad {
        border: .2rem solid v.$primario ;
        padding: 1rem;
        text-align: center;
        &:focus-visible {
            outline: .2rem solid v.$primario ;
            border: none;
        }
    }
    &__agregar-carrito {
        @include m.boton(v.$primario, v.$blanco);
        padding: 2rem 5rem;
        border: none;
        margin-top: 3rem;
        display: block;
        font-size: 1.8rem;
        &:hover {
            background-color: v.$negro;
            cursor: pointer;
        }

    }
}