@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    margin-top: 5rem;
    background-color: v.$negro;
    padding: 5rem 0;

    &__contenido {
        @include m.tablet {
            display: flex;
            justify-content: space-between;
        }
    }

    &__nav {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet {
            flex-direction: row;
            gap: 2rem;
        }

    }
    &__enlace {
        font-size: 2.8rem;
        color: v.$blanco;
        text-decoration: none;
        font-weight: 700;
        &:hover {
            color: v.$primario;
        }
    }
    
    &__copyright {
        font-size: 2.8rem;
        color: v.$blanco;
        font-weight: 700;
        text-align: center;        
    }
}