@use 'base/variables' as v;
@use 'base/mixins' as m;

.cursos {
    background-image: linear-gradient(to right, rgb(0 0 0 / .65), rgb(0 0 0 / .65)), url(../../build/img/cursos_bg.jpg);
    padding: 10rem 0;
    margin-top: 10rem;
    background-size: cover;
    background-position: 50%;
    background-attachment: fixed;

    &__grid {
        @include m.tablet {
            @include m.grid(2, 2rem);
            column-gap: 2rem;
        }
    }

    &__contenido {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet {
            grid-column: 2 / 3;
        }
    }

    &__texto {
        color: v.$blanco;
        text-align: center;
        font-size: 2.4rem;
    }

    &__enlace {
        @include m.boton(v.$primario, v.$blanco);
        background-color: transparent;
       
    }
    

}