@use 'base/variables' as v;
@use 'base/mixins' as m;

.productos {
        &__grid {
        display: grid;
        gap: 4rem;
        @include m.tablet {
            @include m.grid(2, 4rem);
        }
        @include m.desktop {
            @include m.grid(3, 4rem);
        }
    }
}

.producto {
    display: grid;
    grid-template-columns: 2fr 3fr;
    column-gap: 1rem;
    align-items: center;
    &__imagen {
        transition-property: transform;
        transition-duration: .3s;
        &:hover {
            transform: scale(1.1);
        }
    }
    &__nombre {
        font-size: 3rem;
        text-transform: uppercase;
    }
    &__descripcion {
        font-size: 1.6rem;
    }
    &__precio {
        color: v.$primario;
        font-size: 4rem;
        font-weight: 900;
        margin: 0;
    }
    &__enlace {
        @include m.boton(v.$primario, v.$negro);
        background-color: transparent;
        display: block;
        text-align: center;
        &:hover{
            color: v.$blanco;
        }
    }
  
}