@use 'base/variables' as v;
@use 'base/mixins' as m;

.blog {
    &__grid {
        display: grid;
        gap: 4rem;
        @include m.tablet {
            @include m.grid(2, 4rem);
        }
        @include m.desktop {
            @include m.grid(4, 4rem);
        }        
    }
    &__grid--pagina {
        @include m.desktop {
            @include m.grid(3, 4rem);            
        }
    }
}

.entrada {
    max-width: 80rem ;
    margin: 0 auto;
    &__contenido {
        padding: 2rem;
    }
    &__titulo {
        font-weight: 400;
        font-size: 2.8rem;
        margin: 0;
        line-height: 1.2;        
    }
    &__fecha {
        font-size: 1.6rem;
        color: v.$primario;
    }
    &__enlace {
        @include m.boton(v.$primario, v.$negro);
        background-color: transparent;
        display: block;
        text-align: center;
        &:hover {
            color: v.$blanco;
        }
    }
    &__texto {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 5;
        overflow: hidden;
        font-size: 1.6rem;
    }

}