@use 'variables' as v; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-size: 2rem;
    font-family: v.$fuentePrincipal;
    line-height: 1.5;
}
img {
    max-width: 100%;
}
[class*="contenedor"] {
    max-width: 120rem;
    margin: 0 auto;
    width: 95%;
}
[class*="heading"] {
    font-size: 4rem;
    text-align: center;
    font-weight: 900;
    color: v.$primario;
    margin: 5rem 0;

}